import * as actionTypes from './auth.actiontype';
import AuthService from '../../services/AuthService';


export const login = (email,password) => {
    return async dispatch => {
        function loginFail(message) {
            dispatch({ 
                type: actionTypes.LOGIN_FAIL,
                message:message
            });
            return {success:false,error:message}
        }
        function loginSuccess(token, userId, message) {
            dispatch({ 
                type:actionTypes.LOGIN_SUCCESS,
                idToken:token,
                userId:userId,
                message:message
            })
            return {success:true,message:message};
        }
        try {
            dispatch({ 
                type: actionTypes.LOGIN_START 
            });
            let authservice = new AuthService();
            const data = await authservice.login(email,password).then((res)=>{
                 console.log("Response", res);
                if(res.data.success){
                    let resdata = {
                        token:res.data.token,
                        userid:res.data.userid
                    }
                    localStorage.setItem("authUser",JSON.stringify(resdata));
                    return loginSuccess(res.data.token,res.data.userid,res.data.message);
                }else{
                    return loginFail(res.data.message); 
                }
            });
            return data;
        } catch (error) {
            if(error.response){
                
                return loginFail(error.response.data.message);
            }else{
                return loginFail("Something went wrong!");
            }
        }
      }
}

