import React,{Component} from 'react';
import { connect } from 'react-redux';
import * as actions from '../../store/actions/auth/auth.action';
import classes from './SignUp.module.scss';
import SignUpForm from '../../components/signupform'; 
import withAuth from '../../hoc/withAuthentication';
import { store } from 'react-notifications-component';
import 'react-notifications-component/dist/theme.css'
import 'animate.css'

class SignUp extends Component{
      constructor(props){
          super(props)
          this.state = {
              isMessage:false,
              isValid:true,
          }
      }

      signup = async (state) =>{
        const signup = await this.props.signup(state.name.value,state.email.value,state.password.value);
        if(signup.success){
            store.addNotification({
                title: 'Success',
                message: signup.message,
                type: 'default',
                insert: 'top',
                container: 'top-right',
                animationIn: ['animated', 'fadeIn'],
                animationOut: ['animated', 'fadeOut'],
                dismiss: {
                  duration: 2000,
                  onScreen: true,
                },
            })
            this.props.history.push("/signin")
        }
        if(!signup.success){
            store.addNotification({
                title: 'Error',
                message: signup.error,
                type: 'danger',
                insert: 'top',
                container: 'top-right',
                animationIn: ['animated', 'fadeIn'],
                animationOut: ['animated', 'fadeOut'],
                dismiss: {
                  duration: 2000,
                  onScreen: true,
                },
            })
        }
      }

      render(){
          return(
            <div className={classes.signupdiv}>
                <SignUpForm method={this.signup} loading={this.props.loading} classes={classes}/>
            </div>
          )
      }
}

const mapStateToProps = state => {
    return {
        loading: state.authReducer.loading,
        error: state.authReducer.error,
        message:state.authReducer.message,
    };
}

const mapDispatchToProps = dispatch => {
    return {
        signup:(name,email,password)=>dispatch(actions.signup(name,email,password))
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(withAuth(SignUp));