import API from "./Api";

class AuthService {
    login = (email, password) => {
        let params = {
            email:email,
            password:password
        }
        return API.post("/login",params)
    }

    signup = (name, email, password) => {
        const headers = {
            'Url': "http://3.135.194.11:5000"
        }
        let params = {
            name: name,
            email:email,
            password:password
        }
        return API.post("/signup",params,{headers: headers})
    }

    verify = (email,token) => {
        
        let params = {
            email:email,
            token:token
        }
        return API.post("/verifyemail",params)
    }

    forgetPass = (email) => {
        const headers = {
            'Url': "http://3.135.194.11:5000"
        }
        let params = {
            email:email
        }

        return API.post("/forgetpassword",params,{headers: headers})
    }

    resetPass = (password,token) => {
        const headers = {
            'Url': "http://3.135.194.11:5000"
        }
        let params = {
            password:password,
            token:token
        }
        return API.post("/resetpassword",params,{headers: headers})
    }

    updateProfile = (name) => {
        let auth = JSON.parse(localStorage.getItem('authUser'));
        const headers = {
            'Authorization': auth.token
        }
        let params = {
            name:name
        }
        return API.post("/updateprofile",params,{headers: headers})
    }

    changePassword = (oldpassword,password) => {
        let auth = JSON.parse(localStorage.getItem('authUser'));
        console.log("Auth", auth);
        const headers = {
            'Authorization': auth.token
        }
        let params = {
            oldpassword:oldpassword,
            password:password,
            userid: auth.userId
        }
        return API.post("/changepassword",params,{headers: headers})
    }

    getProfile = () => {
        let auth = JSON.parse(localStorage.getItem('authUser'));
        const headers = {
            'Authorization': auth.token
        }
        return API.get("/profile",{headers:headers});
    }
}

export default AuthService;