import React from 'react';
import { withRouter } from 'react-router-dom';
import * as ROUTES from '../routes/routesConstant'; 
import {compose} from 'recompose';

const authroute = [
     'dashboard',
     'adddocument',
     'editdocument',
     'documents',
     'cms',
     'flipbook',
     'photobooth'
]

const nonauthroute = [
    'signup',
    'signin',
    'verify',
    'reset',
    'forgetpass'
]

const withAuthentication = Component => {
    class WithAuthentication extends React.Component{

        componentDidMount(){
            let auth = JSON.parse(localStorage.getItem("authUser"));
            if(!auth && authroute.includes(window.location.pathname.split('/')[1])){
                this.props.history.push(ROUTES.SIGN_IN);
            }
            if(auth && nonauthroute.includes(window.location.pathname.split('/')[1])){
                this.props.history.push(ROUTES.DASHBOARD);
            }
            if(auth && window.location.pathname === "/"){
                this.props.history.push(ROUTES.DASHBOARD);
            }
        }
        render() {
            return <Component {...this.props}/>;
        } 
    }

    return compose(
        withRouter
    )(WithAuthentication);
}

export default withAuthentication;