import React from 'react';
import useForm from './form';
import {Link} from 'react-router-dom';

const SignUpForm = (props) => {
    const stateSchema = {
        name:  {value: '', error:''},
        email: { value: '', error: '' },
        password: { value: '', error: '' }
    };

    const validationStateSchema = {
        name: {
            required: true
        },
        email: {
          required: true,
          validator: {
            regEx: /^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i,
            error: 'Invalid email format.'
          },
        },
        password: {
          required: true
        }
      };

      const { state, handleOnChange, handleOnSubmit, disable } = useForm(
        stateSchema,
        validationStateSchema,
        props.method
      );

      return (
           <form id="signup-form" onSubmit={handleOnSubmit} className={props.classes.formsignup}>
               <div className="bg-white rounded box-shadow p-5 h-100 justify-content-center align-items-center">
                    <div className="text-center">
                       <h1 className="h3 mb-3 font-weight-normal">Sign Up</h1>
                    </div>
                    <div className="form-group">
                      <label htmlFor="name" className="sr-only">Name</label>
                      <input onChange={handleOnChange} name="name" type="text" id="name" className="form-control" placeholder="Name"/>
                      {state.name.error && <p className="invalid-feedback">{state.name.error}</p>}
                    </div>
                    <div className="form-group">
                       <label htmlFor="email" className="sr-only">Email address</label>
                       <input onChange={handleOnChange} name="email" type="email" id="email" className="form-control" placeholder="Email address"/>
                       {state.email.error && <p className="invalid-feedback">{state.email.error}</p>}
                    </div>
                    <div className="form-group">
                       <label htmlFor="password" className="sr-only">Password</label>
                       <input onChange={handleOnChange} name="password" type="password" id="password" className="form-control" placeholder="Password"/>
                       {state.password.error && <p className="invalid-feedback">{state.password.error}</p>}
                    </div>
                    <button disabled={props.loading || disable?true:false} className="btn btn-lg btn-primary btn-block" type="submit">Sign up{props.loading?(<i className="fa fa-circle-o-notch fa-spin ml-3"></i>):null}</button>
                    <p className="text-center">
                        <Link className="btn" to="/signin">Already have an account?</Link>
                    </p>
                </div>
            </form>
      );
}

export default SignUpForm;

